import React, { useState, useContext } from 'react';

import Card from '../../shared/components/UIElements/Card';
import Button from '../../shared/components/FormElements/Button';
import Modal from '../../shared/components/UIElements/Modal';
import Map from '../../shared/components/UIElements/Map';
import ErrorModal from '../../shared/components/UIElements/ErrorModal';
import LoadingSpinner from '../../shared/components/UIElements/LoadingSpinner';
import { AuthContext } from '../../shared/context/auth-context';
import { useHttpClient } from '../../shared/hooks/http-hook';
import './PlaceItem.css';

const PlaceItem = (props) => {
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  const auth = useContext(AuthContext);
  const [showMap, setShowMap] = useState(false);
  const [showPlaceDetail, setShowPlaceDetail] = useState(false);
  const [showConfirmModal, setShowConfirmModal] = useState(false);

  const openPlaceDetailHandler = () => setShowPlaceDetail(true);
  const closePlaceDetailHandler = () => setShowPlaceDetail(false);

  const openMapHandler = () => setShowMap(true);
  const closeMapHandler = () => setShowMap(false);

  // const placeCoordinates = JSON.stringify(props.location, null, 4);
  console.log(props);

  const showDeleteWarningHandler = () => {
    setShowConfirmModal(true);
  };
  const cancelDeleteHandler = () => {
    setShowConfirmModal(false);
  };

  const confirmDeleteHandler = async () => {
    setShowConfirmModal(false);
    try {
      await sendRequest(
        `${process.env.REACT_APP_BACKEND_URL}/places/${props.id}`,
        'DELETE',
        null,
        {
          Authorization: 'Bearer ' + auth.token,
        }
      );
      props.onDelete(props.id);
    } catch (err) {}
  };

  return (
    <React.Fragment>
      <ErrorModal error={error} onClear={clearError} />
      <Modal
        show={showPlaceDetail}
        onCancel={closePlaceDetailHandler}
        header={props.title}
        contentClass='place-item__modal-content'
        footerClass='place-item__modal-actions'
        footer={<Button onClick={closePlaceDetailHandler}>CLOSE</Button>}
      >
        <div className='place-detail-container'>
          <div className='place-detail'>
            <div className='place-detail__image_wrap'>
              <img
                className='place-detail__image'
                src={`${process.env.REACT_APP_ASSET_URL}/${props.image}`}
                alt={props.title}
              />
            </div>
            <div className='place-detail__wrap'>
              <h3>{props.title}</h3>
              <p>First added by: {props.creatorId}</p>
              <p className='detail__desc'>{props.description}</p>
              <p>Address: {props.address}</p>
              <p>
                Coordinates: {props.coordinates.lat}, {props.coordinates.lng}
              </p>
            </div>
          </div>
        </div>
      </Modal>
      <Modal
        show={showMap}
        onCancel={closeMapHandler}
        header={props.address}
        contentClass='place-item__modal-content'
        footerClass='place-item__modal-actions'
        footer={<Button onClick={closeMapHandler}>CLOSE</Button>}
      >
        <div className='map-container'>
          <Map center={props.coordinates} zoom={16} />
        </div>
      </Modal>
      <Modal
        show={showConfirmModal}
        onCancel={cancelDeleteHandler}
        header='Are you sure?'
        footerClass='place-item__modal-actions'
        footer={
          <React.Fragment>
            <Button inverse onClick={cancelDeleteHandler}>
              CANCEL
            </Button>
            <Button danger onClick={confirmDeleteHandler}>
              DELETE
            </Button>
          </React.Fragment>
        }
      >
        <p>
          Do you want to proceed and delete
          <br />
          <strong>&quot;{props.title}&quot;</strong>?
        </p>
        <p>Please note, a deleted place cannot be recovered.</p>
      </Modal>
      <li className='place-item'>
        <Card className='place-item__content card_place'>
          {isLoading && <LoadingSpinner asOverlay />}
          <div className='place-item__image'>
            <img
              src={`${process.env.REACT_APP_ASSET_URL}/${props.image}`}
              alt={props.title}
            />
          </div>
          <div className='place-item__info'>
            <h2>{props.title}</h2>
            <h3 className='place-item__address'>{props.address}</h3>
            <p className='place-item__desc'>{props.description}</p>
          </div>
          <div className='place-item__actions'>
            <Button inverse onClick={openPlaceDetailHandler}>
              VIEW PLACE
            </Button>
            <Button className='plain' inverse onClick={openMapHandler}>
              VIEW MAP
            </Button>

            {auth.userId === props.creatorId && <br />}

            {auth.userId === props.creatorId && (
              <Button className='plain' inverse to={`/places/${props.id}`}>
                EDIT
              </Button>
            )}

            {auth.userId === props.creatorId && (
              <Button
                className='plain'
                danger
                onClick={showDeleteWarningHandler}
              >
                DELETE
              </Button>
            )}
          </div>
        </Card>
      </li>
    </React.Fragment>
  );
};

export default PlaceItem;
